.intro{
	position:fixed;
	left:0px;
	top:0px;
	width:100%;
	height:100%;
	background:url('/assets/img/bg-temp.jpg') no-repeat;
	background-size:cover;

	&__content{
		position:absolute;
		width:100%;
		top:50%;
		-ms-transform:translateY(-50%);
		-webkit-transform:translateY(-50%);
		transform:translateY(-50%);
		text-align: center;
		font-family:$default-font;


		h1{
			margin:20px auto 0px auto;
			padding:0px;
			font-family:$default-font;
			font-size:74px;
			color:$text-gray;
			max-width: 90%;
			strong{
				color:#FFF;
			}

			@media screen and (max-width:$tablet-portrait){
				font-size:60px;
			}

			@media screen and (max-width:$sub-tablet){
				font-size:44px;
			}
		}

		p{
			color:$text-gray;
			font-weight: bold;
			margin:10px 0; 
			padding:0px;

			&.large{
				font-size:60px;
				line-height:20px;
				@media screen and (max-width:$tablet-portrait){
					font-size:52px;
				}
				@media screen and (max-width:$sub-tablet){
					font-size:38px;
					line-height:2px;
				}
			}

			&.small{
				font-size:24px;
			}
		}

		&__img{
			display:inline-block;
			width:120px;
			height:120px;
			margin:20px auto;
			-webkit-border-radius: 50%;
			-moz-border-radius: 50%;
			border-radius: 50%;
			border:6px solid #FFF;
			background:url('/assets/img/me-bg-temp.png') no-repeat;
			background-size:cover;
		}
	}

	.btn_down{
		position:absolute;
		left:50%;
		bottom:0px;
		margin-left:-30px;
		width:60px;
		height:60px;
		background:$btn-gray;
		background-size:cover;
		cursor:pointer;

		&:hover{
			background:$btn-gray-hover;
		}
		i{
			font-weight: bold;
		    font-size: 64px;
		    text-align: center;
		    width:100%;
		    color:$arrow-blue;
		}
	}
}